@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
  --horiz-margin: 2vh;
  --vert-margin: 1%;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'poppins', sans-serif;
  background-color: var(--bg-color);
  color: white;
  height: 100vh;
}
a {
  color: var(--main-color);
}
.App {
  display: flex;
}
.header-and-main-component-container {
  flex-grow: 1;
  flex-shrink: 100;
  height: 100vh;
}
.menu-container, .retracted-menu {
    width: 250px !important;
    background-color: var(--light-bg-color);
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
}
.menu-container .logo-container, .retracted-menu .logo-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 4vh;
}
.menu-container .logo-container .logo {
    fill: var(--main-color);
    width: 50px;
    height: 50px;
    margin-bottom: 10px;
}
.menu-container .logo-container h1 {
    font-size: 18px;
    font-weight: 600;
}
.menu-container .retract-menu-button, .retracted-menu .retract-menu-button {
    position: absolute;
    top: 20vh;
    right: -15px;
    background-color: var(--light-bg-color);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    box-shadow: var(--box-shadow);
    cursor: pointer;
}
.menu-container .retract-menu-button svg, .retracted-menu .retract-menu-button svg {
    width: 50%;
    height: 50%;
}
.menu-container .links-container, .retracted-menu .links-container {
    display: flex;
    flex-direction: column;
}
.menu-container .links-container a, .retracted-menu .links-container a {
    display: flex;
    align-items: center;
    color: white;
    text-decoration: none;
    padding: 1.5vh 0;
    padding-left: 5%;
    position: relative;
    transition: background-color .1s;
}
.menu-container .links-container a.active {
    color: var(--main-color);
}
.menu-container .links-container a.active::before {
    content: '';
    display: inline-block;
    width: 2px;
    height: 27px;
    background-color: var(--main-color);
    position: absolute;
    left: 0;
    border-radius: 0 5px 5px 0;
}
.links-container svg {
    width: 25px;
    height: 25px;
    margin-right: 10px;
    position: relative;
}
.links-container a.active svg path {
    stroke: var(--main-color);
}
.links-container svg path {
    stroke: white;
}
.menu-container .links-container a:hover {
    background-color: rgba(100, 100, 100, .1);
}
.menu-container .bottom-menu-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 20px;
    margin-bottom: 20px;
}
.bottom-menu-container a {
    display: flex;
    align-items: center;
    color: white;
    text-decoration: none;
    opacity: .3;
    font-size: 14px;
    font-weight: 300;
    margin-bottom: 10px;
    transition: opacity .2s;
}
.bottom-menu-container svg {
    margin-right: 2%;
    width: 20px;
    height: 20px;
}
.bottom-menu-container a:hover {
    opacity: .6;
}


/* RETRACTED MENU */
.retracted-menu {
    width: 50px;
}
.retracted-menu .retract-menu-button {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.retracted-menu .logo-container {
    margin-top: 2vh;
}
.retracted-menu .logo-container h1 {
    display: none;
}
.retracted-menu .logo-container .logo {
    fill: var(--main-color);
    width: 30px;
    height: 30px;
}
.retracted-menu .links-container {
    align-items: center;
}
.retracted-menu .links-container p {
    display: none;
}
.retracted-menu .links-container a:hover {
    background-color: rgba(100, 100, 100, .15);
}
.retracted-menu .links-container a {
    width: 60%;
    justify-content: center;
    border-radius: 5px;
    padding-left: 0;
    padding: 5px 0;
    margin: 10px 0;
}
.retracted-menu .bottom-menu-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2vh;
}
.retracted-menu .bottom-menu-container a {
    margin: 10px 0;
}
.retracted-menu .bottom-menu-container p {
    display: none;
}
.retracted-menu .bottom-menu-container svg {
    margin-right: 0;
    width: 25px;
    height: 25px;
}


