}

@-webkit-keyframes loader {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    } to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

@keyframes loader {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    } to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}
