.success-change-anim {
    position: absolute;
    left: -35px;
    width: 30px;
    height: 100%;
}
.success-change-anim path {
    -webkit-animation:check 1s forwards cubic-bezier(.99,-.1,.01,1.02);
            animation:check 1s forwards cubic-bezier(.99,-.1,.01,1.02);
    stroke: white;
}
@-webkit-keyframes check {to {stroke-dashoffset: 0;}}
@keyframes check {to {stroke-dashoffset: 0;}}


